.menu-squares-container {
    padding: 5rem 0 3.1rem 0;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-column-gap: 2rem;
    grid-row-gap: 2rem;
    max-width: 1060px;
    margin: 0 auto;
}

.one {
    grid-area: 1 / 1 / 3 / 2;
    height: 40rem;
    /* background-image: url(../assets/images/Борщ-тел-чб.png); */
    background-position: center;
    background-size: cover;
    margin-left: 32px;

    background-image: url(../assets/images/Борщ\ комп.jpg);
    
}

.two {
    grid-area: 1 / 2 / 2 / 3;
    margin-right: 32px;
    background-image: url(../assets/coffee-cup.jpg);
    
}

.three {
    grid-area: 2 / 2 / 3 / 3;
    margin-right: 32px;
    background-image: url(../assets/images/Паштет\ в\ винном\ .jpg);

}


.card {
    border-radius: 20px;
    /* background: #f5f5f5; */
    position: relative;
    padding: 1.8rem;
    border: 2px solid #c3c6ce;
    transition: 0.5s ease-out;
    overflow: visible;

    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

.card-details {
    color: var(--bg-color-darker);
    height: 100%;
    gap: .5em;
    display: grid;
    place-content: center;
}

.card-button {
    transform: translate(-50%, 125%);
    width: 60%;
    border-radius: 1rem;
    border: none;
    background-color: var(--main-color);
    color: #fff;
    font-size: 1rem;
    padding: .5rem 1rem;
    position: absolute;
    left: 50%;
    bottom: 0;
    opacity: 0;
    transition: 0.3s ease-out;
    cursor: pointer;
}

.text-body {
    color: var(--bg-color-darker);
    font-size: 1.1em;
}

/*Text*/
.text-title {
    font-size: 2.5em;
    font-weight: bold;
}

/*Hover*/
.card:hover {
    border-color: var(--main-color);
    box-shadow: 0 4px 18px 0 rgba(0, 0, 0, 0.25);
}

.card:hover .card-button {
    transform: translate(-50%, 50%);
    opacity: 1;
}




@media(max-width: 800px) {
    .menu-squares-container {
        grid-template-columns: 1fr;
        grid-template-rows: repeat(3, 1fr);
        max-width: 800px;
    }

    .one {
        grid-area: 1 / 1 / 2 / 2;
        margin: 0;
    }

    .two {
        grid-area: 2 / 1 / 3 / 2;
        margin: 0;
    }

    .three {
        grid-area: 3 / 1 / 4 / 2;
        margin: 0;
    }

    .card {
        height: 100%;
        cursor: pointer;
        padding: 3rem 0;
        margin: 0 2rem;
    }

    .text-body {
        display: none;
    }

    /* .card-button {
        display: none;
    } */
}

@media(max-width: 600px) {
    .text-title {
        font-size: 2rem;
        text-align: center;
    }
}